.footer {
  &__content {
    padding: 0 247px;
    padding-top: 98px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    padding-bottom: 98px;
  }

  &__column {


    &:nth-child(2) {
      margin-left: -55px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__list {
    &--row {
      display: flex;
      align-items: center;

      .footer__item {
        margin-bottom: 0;

      }

      .footer__item + .footer__item {
        margin-left: 43px;
      }
    }
  }

  &__link {
    font-size: 13px;
    color: var(--tundora-color);

    &--row {
      font-weight: 700;
      font-size: 13px;
    }
  }

  &__title {
    margin-bottom: 37px;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
  }

  &__form {
    margin-bottom: 20px;
    width: 649px;
  }

  &__text {
    font-weight: 500;
    font-size: 13px;
    color: var(--tundora-color);
  }



  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #eee;
    padding: 40px 0;
  }

  &__social {
    margin-right: 13px;
  }
}

.subscribe-form {
  position:  relative;

  &__title {
    margin-bottom: 35px;
    font-weight: 500;
    font-size: 13px;
    color: var(--tundora-color);
  }

  &__btn {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 2;
    padding: 12px;
    font-weight: 500;
    font-size: 13px;
    color: var(--dark-color );

  }

  &__field {
    display: block;
    border-bottom: 2px solid var(--dark-color);
    padding-bottom: 11px;
    width: 100%;

    input {
      border: none;
      padding-right: 80px;
      width: 100%;
      font-size: 13px;
      color: var(--dark-color);

      &:focus {
        outline: none;
      }

      &::placeholder {
        color: rgba(#454545, 0.5);
      }
    }
  }
}


.social {
  display: flex;
  align-items: center;

  &__item + &__item {
    margin-left: 29px;
  }

  &__link {
    display: block;
    width: 16px;
    height: 16px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--twitter {
      background-image: url('../img/twit.png');
    }

    &--facebook {
      background-image: url('../img/fb.png');
    }

    &--inst {
      background-image: url('../img/inst.png');
    }

    &--pinterest {
      background-image: url('../img/pint.png');
    }
  }
}
