// colors
:root {
  // base
  --font-family: "Jost", sans-serif;
  --content-width: 1820px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white-color: #fff;
  --black-color: #000;
  --dark-color: #151515;
  --quil-gray: #e5e5e3;
  --gray-color: #858585;
  --tundora-color: #454545;
}
