.market-banners {
  padding-top: 24px;
}

.market-banners__container {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.market-banner {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
  min-height: 420px;
  width: 50%;
  background-color: var(--dark-color);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;


  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(#d70808, 0.1);
  }

  &--men {
    background-image: url('../img/banner-1.jpg');
  }

  &--fit {
    background-image: url('../img/banner-2.jpg');
  }

  &__title {
    position: relative;
    z-index: 1;
    font-size: 48px;
    font-weight: 500;
    margin-bottom: 20px;
    color: var(--white-color);
  }

  &__link {
    font-size: 15px;
    color: var(--white-color);
  }
}
