.product {
  position: relative;

  &__image {
    margin-bottom: 20px;
  }

  &__title {
    margin-bottom: 8px;
    font-size: 13px;
    color: var(--tundora-color);

    a::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }

  &__price {
    font-size: 13px;
    font-weight: 700;
    color: var(--dark-color);
  }
}
