.main-products {
  &__container {
    padding-top: 75px;
    padding-bottom: 75px;
  }

  &__nav {
    display: flex;
    justify-content: center;
    margin-bottom: 45px;
  }

  &__item {
    margin: 0 17px;
  }

  &__btn-centered {
    text-align: center;
  }

  &__more {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 95px;
    font-size: 13px;
    color: var(--gray-color);

    svg {
      margin-top: 14px;
      width: 12px;
      height: 21px;
      fill: var(--dark-color);
    }
  }

  &__btn {
    font-size: 24px;

    &--active {
      &::before {
        width: 100%;
        transition: width .2s;
      }
    }
  }
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__item {
    // margin-right: 30px;
    margin-bottom: 80px;

    // &:nth-child(5n) {
    //   margin-right: 0;
    // }

    &:nth-last-child(-n+5) {
      margin-bottom: 0;
    }
  }
}
