.hero {
  position: relative;
  height: 100vh;
  height: 1080px;
  min-height: 700px;
  background-color: var(--quil-gray);

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(#000, 0.05);
  }

  &__container {
    position: relative;
    height: 100%;

  }

  &__title {
    margin-bottom: 40px;
    font-weight: 700;
    max-width: 540px;
    font-size: 72px;
    color: var(--dark-color);
  }

  &__descr {
    font-size: 18px;
    color: var(--dark-color);
  }

  &__image {
    position: absolute;
    right: 280px;
    bottom: 0;
    max-height: 90%;
    object-fit: cover;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 260px;
    height: 100%;
  }
}

.banner-slider {
  width: 100%;
  height: 100%;
  overflow: hidden;

  .swiper-slide {
    height: 100%;
  }
}

.swiper-pagination-bullet {
  margin: 0 13px;
  width: 6px;
  height: 6px;
  background-color: var(--white-color);
  opacity: 0.5;

  &-active {
    position: relative;
    background-color: var(--dark-color);
    opacity: 1;


    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -1;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      background-color: var(--white-color);
      transform: translate(-50%, -50%);

    }
  }
}
.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 var(--swiper-pagination-bullet-horizontal-gap, 8px);
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 47px;
}

.product-purchased {
  position: absolute;
  bottom: 47px;
  left: 40px;
  z-index: 2;
  display: flex;
  padding: 10px;
  width: 280px;
  box-shadow: 0px 5px 30px 0px rgba(0,0,0,0.1);
  background-color: #ffffff;
  transform: translate(0);
  transition: transform 0.4s ease;

  &--hidden {
    transform: translate(-100vh);
  }

  &__text {
    margin-bottom: 7px;
    margin-left: 10px;
  }

  &__image {
    flex-shrink: 0;

  }

  &__descr {
    display: block;
    margin-bottom: 5px;
    font-size: 11px;
    color: var(--gray-color);
  }

  &__title {
    margin-bottom: 7px;
    padding-right: 25px;
    min-height: 33px;
    font-size: 15px;
  }

  &__from {
    display: block;
    padding-right: 40px;
    font-style: italic;
    font-size: 11px;
    color: var(--gray-color);
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 9px;
    height: 10px;
    background-image: url("../img/product-close.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

