.banners {
  &__container {
    padding-top: 50px;
    padding-bottom: 30px;
  }

  &-inner {
    padding: 54px 60px;
    padding-right: 54%;
    min-height: 290px;
    background-color: #cfd1d0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &--midseason {
      background-image: url('../img/midseason.jpg');
    }

    &--romance {
      background-image: url('../img/romance.jpg');
    }

    &--access {
      background-image: url('../img/access.jpg');
    }

    &__title {
      font-size: 36px;
      margin-bottom: 15px;
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    flex-shrink: 0;
    width: 33%;


  }

  &__item + &__item  {
    margin-left: 0.6%;
  }





}
