.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 5;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  &__shop-list {
    margin-left: 250px;
  }
}

.logo {
  display: inline-block;
  width: 177px;
  height: 22px;
  svg {
    display: block;
    width: 100%;
    height: 100%;
    fill: var(--dark-color);
    object-fit: cover;
  }
}
